<template>
  <div>



    <div style="margin: 10px 0">
      <div>
        <img src="https://www.scfai.edu.cn/newfy/3little.png" width="306" height="837px" style="float: left">
      </div>
      <div style="float: right;width: 872px;min-height: 658px">
        <div style="float: left;width: 870px;padding-left: 1px;border: #eeeeee 1px solid;margin-bottom: 14px;min-height: 658px" >
          <ul style="float: left;line-height: 38px;color: #7d7c7c;padding-left: 18px;">
            <li v-for="item in goods" :key="item.id" @click="$router.push('/front/notificationDeatil?id=' + item.id)" >
              <!--              <a href="" style="font-size: 14px">{{item.title}}</a>-->
              <span style="font-size: 14px">{{item.title}}</span>
              <span style="position: absolute;right: 250px;font-size: 14px">{{item.publishertime}}</span>
            </li>
            <div style="padding: 10px 0;">
              <el-pagination
                      background
                      @size-change="handleSizeChange"
                      @current-change="handleCurrentChange"
                      :current-page="pageNum"
                      :page-sizes="[4, 8, 12]"
                      :page-size="pageSize"
                      layout="total, prev, pager, next"
                      :total="total">
              </el-pagination>
            </div>
          </ul>


        </div>
      </div>


    </div>

  </div>
</template>

<script>
  export default {
    name: "FrontHome",
    data() {
      return {
        lun: [],
        files: [],
        goods: [],
        pageNum: 1,
        pageSize: 20,
        total: 0,
        searchInput:""
      }
    },
    created() {
      this.load()
    },
    methods: {
      load() {
        this.request.get("/lun").then(res => {
          this.lun = res.data
        })

        this.request.get("/notification/page", {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            name: this.name,
          }
        }).then(res => {
          this.goods = res.data.records
          this.total = res.data.total
        })
      },
      handleSizeChange(pageSize) {
        this.pageSize = pageSize
        this.load()
      },
      handleCurrentChange(pageNum) {
        this.pageNum = pageNum
        this.load()
      },
      searchProduct(){
        this.request.get("/notification/page", {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            name: this.searchInput,
          }
        }).then(res => {
          this.goods = res.data.records
          this.total = res.data.total
        })
      },
    }
  }
</script>

<style>
  .line1 {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .el-input--mini .el-input__inner{
    height: 46px;
  }
</style>
